@use 'global' as *;

.p-company {
  padding-top: rem(100);
  margin-top: rem(-100);
  text-align: center;
  margin-right: rem(25);
  margin-left: rem(25);
  .p-company__inner {
    position: relative;
  }
}
.p-company__top {
  position: absolute;
  top: rem(-45);
  left: 50%;
  transform: translateX(-50%);
  background-color: #f4f2f2;
  padding: rem(30) rem(37.7) rem(40) rem(38.3);
  border-radius: rem(10);
}
.p-company__en {
  font-size: rem(20);
  font-family: $font-sub;
}
.p-company__ja {
  font-size: rem(12);
  font-family: $font-yu;
  margin-bottom: rem(24);
}
.p-company__detail {
  letter-spacing: normal;
  font-size: rem(12);
  font-family: $font-yu;
  border-bottom: 1px dotted #707070;
  line-height: 2.75;
}
.p-company__detail:last-of-type {
  border: none;
  margin-bottom: rem(30);
}
.p-company__button {
  display: block;
  margin: 0 rem(30);
  background-color: #333333;
  color: $white;
  border-radius: rem(3);
  font-size: rem(18);
  font-family: $font-sub;
  padding: rem(9);
}
.p-company__button span {
  display: inline-block;
  width: rem(14.77);
  height: rem(7.07);
  margin: rem(0) rem(0) rem(2) rem(2);
}
