  font-size: rem(18);
  font-family: $font-sub;
  padding: rem(9);
  position: relative;
}
.p-company__button::after {
  border-right: solid rem(1) #fff;
  border-top: solid rem(1) #fff;
  content: "";
  display: block;
  height: rem(10);
  width: rem(10);
  position: absolute;
  right: rem(26);
  top: 30%;
  transform: rotate(135deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.p-company__button.active::after {
  top: 45%;
  transform: rotate(-45deg);
}
