.p-cv__entry,
.p-cv__contact {
  display: block;
  font-size: rem(20);
  text-transform: uppercase;
  color: $white;
  padding: rem(9);
  letter-spacing: 0.1em;
  text-align: center;
  font-family: $font-accent;
  max-width: rem(250);
  margin-inline: auto;
}
.p-cv__entry {
  background-color: $blue;
  margin-bottom: rem(19);
}
.p-cv__contact {
  background-color: $gray2;
}
.p-cv__entry span,
.p-cv__contact span {
  font-size: rem(10);
  display: block;
  letter-spacing: normal;
}
