@use 'global'as *;

.p-contact2 {
  background-color: #dddddd;
  padding-top: rem(113);
}

.p-contact2__title.c-section-title {
  text-align: center;
  position: relative;
  z-index: 2;
}

.p-contact2__form {
  background-color: $white;
  margin: rem(0) rem(10);
  border-radius: rem(10);
  padding: rem(0) rem(15) rem(40) rem(15);
  width: calc(100% - 20px);
  margin-top: rem(-9);
  position: relative;
  z-index: 5;
}

.p-contact2__sub {
  text-align: center;
  font-family: $font-yu;
  padding-top: rem(40);
  margin-bottom: rem(36);
}

.p-contact2_item {
  font-size: rem(14);
  font-family: $font-kaku;
  font-weight: 300;
  margin-bottom: rem(15);
}
.p-contact2__list1 {
  font-size: rem(14);
  font-family: $font-yu;
  margin-bottom: rem(10);
  line-height: 2;
}

.p-contact2__list {
  font-size: rem(14);
  font-family: $font-yu;
  margin-bottom: rem(10);
  line-height: 1.85;
}
.p-contact2__list3 {
  font-size: rem(14);
  font-family: $font-yu;
  line-height: 1.85;
}
.p-contact2__border {
  border-bottom: rem(1) dotted $black;
  margin: rem(0) rem(-5.5) rem(6) rem(-5.5);
}
.p-contact2__text {
  font-size: rem(14);
  font-family: "Noto Sans";
  margin-bottom: rem(40);
}
.p-contact__img {
  max-width: rem(237);
  display: block;
  margin: 0 auto rem(40) auto;
}

.p-contact2__button {
  font-size: rem(14);
  font-family: $font-yu;
  display: block;
  text-align: center;
  border: rem(1) solid $black;
  border-radius: rem(28);
  padding: rem(15.5) rem(0);
  margin: rem(0) rem(25);
}

.p-contact2__snsarea {
  padding: rem(40) rem(25) rem(50) rem(25);
  display: flex;
  gap: 20px;
}
.p-contact__snsarea img {
}

.p-contact__line {
}

