@use 'global' as *;
.p-requirements {
    padding-top: rem(80);
    margin-top: rem(-80);
}

.p-requirements__heading {
    padding-top: rem(40);
    text-align: center;
}

.p-requirements__en {
    font-size: rem(20);
    font-family: $font-accent;
}

.p-requirements__ja {
    font-size: rem(12);
    font-family: $font-kaku;
    font-weight: 300;
    margin-bottom: rem(30);
}

.p-requirements__area {
    padding-left: rem(24);
    padding-right: rem(24);
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: rem(10);
    margin-bottom: rem(30);
}

.p-requirements__items {
    background-color: #f4f2f2;
    border-radius: rem(3);
    text-align: center;
    font-size: rem(15);
    font-family: $font-kaku;
    font-weight: 300;
    padding: rem(9.75) 0;
    letter-spacing: normal;
}

.p-requirements__items.active {
    background-color: $black;
    color: $white;
}

.p-requirements__detail {
    line-height: 1.85;
    display: flex;
    flex-direction: column;
    gap: rem(40);
    margin-bottom: rem(40);
    letter-spacing: normal;
}

.p-requirements__list.active {
    display: block;
}

.p-requirements__list {
    margin-left: rem(10);
    margin-right: rem(10);
    background-color: #f4f2f2;
    border-radius: rem(10);
    font-size: rem(14);
    font-family: $font-yu;
    padding: rem(25) rem(20) rem(25) rem(20);
    display: none;
}

.p-requirements__item {
    padding-top: rem(15);
}

.p-requirements__item2 {
    border-bottom: 1px dotted $black;
    padding-bottom: rem(12);
}

.p-requirements__item2:last-of-type {
    border-bottom: none;
}