@use 'global' as *;

.p-thought {
  position: relative;
  margin-bottom: rem(24);
}
.p-thought::before {
  content: "";
  position: absolute;
  top: rem(-20);
  left: rem(-12);
  background-image: url("../images/hairresortai.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  height: rem(321);
  max-width: rem(412);
  width: 100%;
  z-index: 1;
}
.p-thought__inner {
  padding-top: rem(49);
  position: relative;
  z-index: 3;
}
.p-thought__inner::after {
  content: "";
  position: absolute;
  display: inline-block;
  top: 85%;
  left: 0;
  background-image: url("../images/thought-left.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  max-width: rem(211);
  height: rem(291);
  width: 100%;
  z-index: 1;
}
.p-thought__inner::before {
  content: "";
  position: absolute;
  top: 47%;
  right: 0;
  background-image: url("../images/thought-right.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  max-width: rem(93);
  height: rem(291);
  width: 100%;
  z-index: 4;
}
.p-thought__bg img {
}
.p-thought__title {
  font-size: rem(24);
  font-weight: bold;
  line-height: calc(30 / 24);
  margin-bottom: rem(40);
}
.p-thought__img {
  margin-bottom: rem(40);
  margin-right: calc(50% - 50vw);
  aspect-ratio: 365 / 339;
  object-fit: cover;
  @include mq(md) {
    min-width: rem(365);
    width: 100%;
  }
}
.p-thought__heading {
  font-size: rem(20);
  margin-bottom: rem(18);
}
.p-thought__heading::before {
  content: "";
  background-image: url("../images/check.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  display: inline-block;
  width: rem(18);
  height: rem(18);
  margin-right: rem(5);
}
.p-thought__text {
  font-family: $font-kaku;
  font-weight: 300;
  font-size: rem(15);
  margin-bottom: rem(14);
  background-color: #333333;
  color: $white;
  display: table;
  line-height: 1;
  padding: rem(7) rem(10) rem(7) rem(7);
  letter-spacing: 0.04em;
  position: relative;
  z-index: 10;
}
.p-thought__text:last-child {
  margin-bottom: 0;
}
