@use 'global' as *;
.p-faq {
}
.p-faq__inner.l-inner {
  padding-left: rem(25);
  padding-right: rem(25);
  margin-bottom: rem(40);
}
.p-faq__heading {
  text-align: center;
}
.p-faq__en {
  font-size: rem(20);
  font-family: $font-accent;
  line-height: 2;
}
.p-faq__ja {
  font-size: rem(12);
  font-family: $font-kaku;
  font-weight: 300;
  margin-bottom: rem(16);
  letter-spacing: normall;
}
.p-faq__detail {
  font-size: rem(14);
  font-family: $font-kaku;
  font-weight: 300;
}
.p-faq__q {
  line-height: 3;
  border-bottom: rem(1) solid $black;
  margin-bottom: rem(15);
  position: relative;
}
.p-faq__q::before {
  content: "+";
  position: absolute;
  top: 0;
  right: 0;
}
.p-faq__q.open::before {
  content: "-";
}
.p-faq__a {
  line-height: 1.625;
  margin-bottom: rem(20);
  display: none;
}
.p-faq__img {
  margin: 0 calc(50% - 50vw);
  object-fit: cover;
  @include mq(md) {
    margin: 0;
  }
}
