@use 'global' as *;
.p-strength {
    padding-top: rem(80);
    margin-top: rem(-80);
    padding-bottom: rem(56);
    overflow: hidden;
    @include mq(md) {
        overflow: visible;
    }
}

.p-strength__bg {
    background-color: $black;
    margin-right: rem(10);
    margin-left: rem(10);
    padding: rem(13) rem(25) rem(28);
    border-radius: rem(10);
}

.p-strength__inner {
    padding: 0;
}

.p-strength__titleblock {
    text-align: center;
}

.p-strength__title {
    margin-bottom: rem(18);
}

.p-strength__head {
    color: $white;
    font-size: rem(15);
    margin-bottom: rem(29);
    line-height: 1.7;
}

.p-strength__head span {
    border-bottom: 1px solid $white;
    padding-bottom: rem(2);
}

.p-strength__subtitle {
    background-color: #dddddd;
    display: inline-block;
    padding: rem(15) rem(35);
    margin-bottom: rem(20);
    margin-left: calc(50% - 50vw);
    @include mq(md) {
        margin-left: rem(-35);
    }
}

.p-strength__subtitle-right {
    margin-bottom: rem(20);
    margin-right: calc(50% - 50vw);
    text-align: right;
    @include mq(md) {
        margin-right: rem(-35);
    }
}

.p-strength__subtitle-right span {
    background-color: #dddddd;
    padding: rem(15);
}

.p-strength__subtitle p {
    vertical-align: middle;
    font-size: rem(15);
    line-height: 100%;
}

.p-strength__subtitle span {
    color: $orange;
    font-family: $font-accent;
    font-size: rem(48);
}

.p-strength__subtitle:nth-child(3) {
    line-height: 1.6;
}

.p-strength__text {
    font-size: rem(12);
    line-height: calc(22 / 12);
    color: $white;
    letter-spacing: 0.05em;
    margin-bottom: rem(28);
}

.p-strength__subtitle-right p {
    vertical-align: middle;
    font-size: rem(15);
    line-height: 100%;
}

.p-strength__more {
    margin-top: rem(64);
    text-align: center;
}

.p-strength__more2 {
    margin-bottom: rem(64);
    text-align: center;
}

.p-strength__more2 span {
    border-bottom: 1px solid $black;
}

.p-strength__more2::before {
    content: "";
    display: inline-block;
    width: rem(18);
    height: rem(1);
    background-color: $black;
    transform: rotate(45deg);
    vertical-align: middle;
    margin-right: rem(8);
    margin-top: rem(-2);
}

.p-strength__more2::after {
    content: "";
    display: inline-block;
    width: rem(18);
    height: rem(1);
    background-color: $black;
    transform: rotate(135deg);
    vertical-align: middle;
    margin-left: rem(-2);
    margin-top: rem(-2);
}

.p-strength__heading {
    font-size: rem(15);
    color: $white;
    background-color: $gray2;
    padding: rem(15) rem(193) rem(15) rem(27);
    white-space: nowrap;
    position: relative;
    z-index: 2;
    margin-left: calc(50% - 50vw);
    letter-spacing: 0.01em;
    @include mq(md) {
        margin-left: rem(-26);
    }
}

.p-strength__wrap img {
    margin-top: rem(-23);
    margin-bottom: rem(30);
    margin-right: calc(50% - 50vw);
    width: 100vw;
    padding-left: rem(15);
    object-fit: cover;
    @include mq(md) {
        margin-right: calc(50% - 20vw);
        max-width: rem(364);
    }
}

.p-strength__text2 {
    font-size: rem(12);
    line-height: calc(22 / 12);
    letter-spacing: 0.05em;
    padding-left: rem(15);
    padding-right: rem(15);
    margin-bottom: rem(30);
}