@use 'global' as *;
.p-entry-form {
    background-color: #f4f2f2;
    padding: rem(40) rem(26) rem(40) rem(24);
}

.p-entry-form__heading {
    text-align: center;
}

.p-entry-form__en {
    font-size: rem(20);
    font-family: $font-accent;
    margin-bottom: rem(13);
}

.p-entry-form__ja {
    font-size: rem(12);
    font-family: $font-kaku;
    font-weight: 300;
    line-height: 2;
    margin-bottom: rem(36);
}

.p-entry-form__snsbox {
    display: flex;
    gap: rem(20);
    margin-bottom: rem(40);
}

.p-entry-form__block {
    font-size: rem(14);
    font-family: $font-yu;
}

.p-entry-form__sns {}

.p-entry-form__img {
    object-fit: cover;
}

.p-entry-form__item {}

.p-entry-form__radio label input {}

.p-entry-form__textarea {
    resize: none;
    background: #f4f2f2;
    border: solid 1px #000;
}

.p-entry-form__inputitem {
    line-height: 1.8;
}

.p-entry-form__input {
    margin-left: rem(-1);
}

.p-entry-form__inputitems {
    margin-bottom: rem(10);
}

.p-entry-form__label {
    margin-right: rem(10);
}

.p-entry-form__textbox {
    width: 100%;
    height: rem(38);
    margin-bottom: rem(10);
    background: #f4f2f2;
    border: solid 1px #000;
    padding-left: rem(8);
    padding-right: rem(8);
}

.p-entry-form__textarea {
    width: 100%;
    height: rem(127);
    padding: rem(8);
}

.p-entry-form__button input {
    display: block;
    text-align: center;
    border: 1px solid #000;
    padding: rem(15.5);
    border-radius: rem(28);
    margin: rem(28) rem(10) rem(0) rem(10);
    min-width: rem(320);
    margin-inline: auto;
    background-color: transparent;
    color: $black;
}

.p-entry-form__button {
    position: relative;
}

.p-entry-form__button::before {
    content: ">";
    position: absolute;
    top: 40%;
    left: 42%;
    transform: translate(-50%, -50%);
    width: rem(14);
    height: rem(14);
    z-index: 10;
    display: block;
}