@use 'global'as *;

.p-shop-list {
  padding: 0 rem(25) rem(40);
  letter-spacing: normal;
}
.p-shop-list__heading {
  text-align: center;
}
.p-shop-list__en {
  font-size: rem(20);
  font-family: $font-sub;
}
.p-shop-list__ja {
  font-size: rem(12);
  font-family: $font-yu;
  margin-bottom: rem(25);
}
.p-shop-list__area {
  text-align: center;
}
.p-shop-list__item1 {
}
.p-shop-list__salon {
  font-size: rem(13);
  font-family: $font-accent;
  background-color: $blue;
  color: $white;
  line-height: 1.6925;
  margin-bottom: rem(10);
}
.p-shop-list__subname {
  font-size: rem(12);
  font-family: $font-accent;
  margin-bottom: rem(20);
}
.p-shop-list__name img {
  display: block;
  width: 100%;
  object-fit: cover;
}
.p-shop-list__text {
  font-size: rem(12);
  font-family: $font-kaku;
  font-weight: 300;
  line-height: 1.7;
}
.p-shop-list__item2 {
  margin-top: rem(30);
}
.p-shop-list__imagearea {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: rem(10);
  margin-bottom: rem(30);
}
.p-shop-list__name1 {
  width: calc(50% - 5px);
  height: 50%;
  object-fit: cover;
  margin: 0 auto;
}
.p-shop-list__block {
}
.p-shop-list__item3 {
  margin-bottom: rem(30);
}
.p-shop-list__item5 {
}
.p-shop-list__item5 {
}
.p-shop-list__item6 {
  margin-top: rem(35);
}
.p-shop-list__name2 {
  width: calc(50% - 5px);
  height: 50%;
  object-fit: cover;
  margin: 0 auto;
}
.p-shop-list__salon2 {
  font-size: rem(13);
  font-family: $font-accent;
  background-color: $blue;
  color: $white;
  line-height: 1.6925;
  margin-bottom: rem(20);
}
