@use 'global' as *;

.p-flow {
  padding-top: rem(116);
  margin-top: -80px;
}
.p-flow__inner.l-inner {
  padding-left: rem(35);
  padding-right: rem(35);
}
.p-flow__title {
}
.p-flow__head {
  font-size: rem(12);
  text-align: center;
  margin-bottom: rem(53);
}
.p-flow__box {
  padding: rem(30) rem(12);
  background-color: #f4f2f2;
  position: relative;
  margin-bottom: rem(15);
  width: 100%;
}
.p-flow__number {
  color: $orange;
  font-size: rem(20);
  font-family: $font-sub;
  position: absolute;
  top: 0%;
  left: rem(20);
  transform: translateY(-50%);
}
.p-flow__text {
  font-size: rem(15);
  line-height: calc(28 / 15);
}
.p-flow__wrap {
  display: flex;
  align-items: start;
  min-width: rem(254);
  margin-bottom: rem(64);
}
.p-flow__left {
  min-width: rem(254);
}
.p-flow__right {
  width: rem(16);
  min-height: rem(335);
  margin-inline: auto;
  text-align: center;
  position: relative;
}
.p-flow__text2 {
  font-size: rem(15);
  font-family: $font-kaku;
  writing-mode: vertical-rl;
  margin-inline: auto;
  position: relative;
}
.p-flow__text2::after {
  content: "";
  position: absolute;
  top: 61%;
  left: 67%;
  transform: translateX(-50%);
  background-image: url("../images/flow-arrow.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  height: rem(400);
  width: rem(11);
  z-index: 1;
}
.p-flow__money {
  position: absolute;
  top: 80%;
  left: 60%;
  transform: translateX(-50%);
  font-size: rem(15);
  color: $orange;
  white-space: nowrap;
  background-color: $gray3;
  z-index: 5;
}
.p-flow__money2 {
  position: absolute;
  top: 110%;
  left: 70%;
  transform: translateX(-50%);
  white-space: nowrap;
  font-size: rem(15);
  color: $orange;
  background-color: $gray3;
  z-index: 5;
}
.p-flow__img {
  object-fit: cover;
}
