@use 'global'as *;

.p-thanks {
  background-color: #dddddd;
  padding-top: rem(113);
}
.p-thanks__inner {
  background-color: $white;
  border-radius: rem(10);
  margin: rem(0) rem(10) rem(40);
  text-align: center;
  padding: rem(40) rem(32) rem(50) rem(32);
}
.p-thanks__heading {
  font-size: rem(20);
  font-family: $font-yu;
  margin-bottom: rem(20);
}

.p-thanks__sub {
  font-size: rem(12);
  text-align: center;
  font-family: $font-yu;
  margin-bottom: rem(65);
  letter-spacing: normal;
  line-height: 2;
}

.p-thanks__img {
  max-width: rem(237);
  display: block;
  margin: 0 auto rem(24) auto;
}

.p-thanks__followme {
  margin-top: rem(40);
  text-align: center;
  font-size: rem(12);
  font-family: $font-yu;
}
.p-thanks__back {
  font-size: rem(12);
  font-family: $font-sub;
  border-bottom: 1px solid #000;
}
.p-thanks__snsarea {
  padding: rem(1) rem(25) rem(50) rem(25);
  display: flex;
  gap: 20px;
}
