@use 'global' as *;
.p-company-pull {
    background-color: $white;
}

.p-company-pull__inner {
    text-align: center;
    margin-right: rem(25);
    margin-left: rem(25);
    background-color: #f4f2f2;
    padding: rem(40) rem(38) rem(40) rem(38);
    border-radius: rem(10);
}

.p-company-pull__top {}

.p-company-pull__heading {}

.p-company-pull__en {
    font-size: rem(20);
    font-family: $font-sub;
}

.p-company-pull__ja {
    font-size: rem(12);
    font-family: $font-yu;
    margin-bottom: rem(24);
}

.p-company-pull__detail {
    letter-spacing: normal;
    font-size: rem(12);
    font-family: $font-yu;
    border-bottom: 1px dotted #707070;
    line-height: 2.7;
}

.p-company-pull__detail:last-of-type {
    border: none;
    margin-bottom: rem(30);
}

.p-company-pull__history {
    letter-spacing: normal;
    font-size: rem(12);
    font-family: $font-yu;
    margin-bottom: rem(30);
    display: none;
}

.p-company-pull__history.active {
    display: block;
}

.p-company-pull__date {
    padding-top: rem(6.5);
}

.p-company-pull__open {
    border-bottom: 1px dotted #707070;
    padding-bottom: rem(6.5);
}

.p-company-pull__open:last-child {
    border-bottom: none;
}

.p-company-pull__button {
    display: block;
    margin: 0 rem(30);
    background-color: #333333;
    color: $white;
    border-radius: rem(3);
    font-size: rem(18);
    font-family: $font-sub;
    padding: rem(9);
}

.p-company-pull__button span {
    display: inline-block;
    width: rem(14.77);
    height: rem(7.07);
    margin: rem(0) rem(0) rem(2) rem(2);
}