@use 'global' as *;
.p-footer {
    position: fixed;
    bottom: 0;
    z-index: 8000;
    width: 100%;
    @include mq(md) {
        max-width: rem(390);
    }
}

.p-footer.open {
    z-index: 0;
}

.p-footer__inner {
    display: flex;
    align-items: center;
}

.p-footer__entry,
.p-footer__contact {
    font-size: rem(20);
    text-transform: uppercase;
    color: $white;
    padding: rem(14);
    letter-spacing: 0.1em;
    width: 50%;
    text-align: center;
    font-family: $font-accent;
}

.p-footer__entry {
    background-color: $blue;
}

.p-footer__entry span {
    font-size: rem(10);
    display: block;
    letter-spacing: normal;
}

.p-footer__contact {
    background-color: $gray2;
}

.p-footer__contact span {
    letter-spacing: normal;
    font-size: rem(10);
    display: block;
}

.p-main-footer {
    background-color: $sub-black;
    padding: rem(0) rem(7) rem(80);
}

.p-main-footer__area {
    text-align: center;
    color: $white;
    letter-spacing: normal;
}

.p-main-footer-logo {
    max-width: rem(76);
    margin: 0 auto;
    padding: rem(39) rem(0) rem(17.5) rem(0);
}

.p-main-footer__sub {
    font-family: $font-kaku;
    font-size: rem(10);
    font-weight: 300;
    margin-bottom: rem(34);
}

.p-main-footer__textblock {
    margin-bottom: rem(31);
}

.p-main-footer__text {
    font-size: rem(12);
    font-family: $font-kaku;
    font-weight: 300;
    line-height: 2;
}

.p-entry__block.p-cv {}

.p-cv__text {}

.p-cv__entry {}

.p-cv__contact {}

.p-main-footer__snsarea {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: rem(20);
    border-bottom: rem(1) solid $white;
    padding-bottom: rem(25);
}

.p-main-footer__snsinsta {
    max-width: rem(24);
}

.p-main-footer__snstiktok {
    max-width: rem(24);
}

.p-main-footer__snsline {
    max-width: rem(24);
}

.p-main-footer__copyright {
    font-size: rem(10);
    font-family: $font-yu;
    padding: rem(20) rem(0);
}