@use 'global' as *;

.p-voice {
  padding-top: rem(116);
  margin-top: rem(-80);
  margin-bottom: rem(64);
}
.p-voice__inner {
  padding-left: rem(30);
  padding-right: rem(20);
}
.p-voice__title {
}
.p-voice__text {
  text-align: center;
  font-size: rem(12);
  line-height: 2;
  margin-bottom: rem(30);
}
.p-voice__card {
  display: block;
  position: relative;
  margin-bottom: rem(20);
  cursor: pointer;
}
.p-voice__number {
  width: rem(48);
  height: rem(48);
  background-color: $blue;
  border-radius: 50%;
  color: $white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: rem(12);
  position: absolute;
  top: rem(-15);
  left: rem(-15);
}
.p-voice__wrap {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: rem(54) rem(29);
}
.p-voice__box {
  position: absolute;
  bottom: rem(-34);
  left: rem(-10);
}
.p-voice__position,
.p-voice__name {
  background-color: $gray2;
  font-size: rem(12);
  padding: rem(5) rem(4);
  display: inline-block;
  white-space: nowrap;
  margin-bottom: rem(3);
  color: $white;
  letter-spacing: 0.09em;
}
.p-voice__position span {
  background-color: $gray2;
  display: block;
}
.p-voice__name {
  background-color: $gray2;
  font-size: rem(12);
}
.p-voice__school {
  font-size: rem(12);
  color: $orange;
  margin-top: rem(6);
  padding-left: rem(10);
}
