@use 'global' as *;
.p-header__inner {
    display: flex;
    background-color: $sub-black;
    position: fixed;
    height: rem(73);
    justify-content: space-between;
    align-items: center;
    z-index: 100;
    @include mq(md) {
        max-width: rem(390);
        width: 100%;
    }
}

.p-header__logo {
    height: rem(42);
    width: rem(45);
    z-index: 9999;
    margin-left: rem(6);
}

.p-header__menu {
    color: $white;
    font-size: rem(10);
    text-transform: uppercase;
    margin-top: rem(6);
    font-family: $font-sub;
    letter-spacing: 0.15em;
    text-align: right;
}

.p-header__hamburger {
    height: inherit;
}

.p-header__hamburger {
    position: relative;
    z-index: 9999;
    width: rem(45);
    height: inherit;
    transition: ease 0.5s;
}

.p-header__hamburger span {
    position: absolute;
    display: block;
    width: rem(30);
    height: rem(1.5);
    border-radius: rem(4);
    background-color: #fff;
    transition: ease 0.3s;
}

.p-header__hamburger span:nth-child(1) {
    top: rem(21);
    left: rem(14);
}

.p-header__hamburger span:nth-child(2) {
    top: rem(28);
    left: rem(14);
}

.p-header__hamburger.open span:nth-child(1) {
    top: rem(22);
    transform: rotate(-160deg);
}

.p-header__hamburger.open span:nth-child(2) {
    left: rem(14);
    top: rem(22);
    transform: rotate(160deg);
}

.p-header__drawer {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background-color: #1d1d1d;
    width: 100%;
    height: 100vh;
    z-index: 9990;
    transition: ease 0.3s;
    transform: translateY(-101%);
    overflow: auto;
    color: $white;
    @include mq(md) {
        max-width: rem(390);
        width: 100%;
        margin-inline: auto;
    }
}

.p-header__drawer.open {
    position: fixed;
    transform: translateX(0);
}

.p-header__drawer-nav-items {
    padding-top: rem(120);
    text-align: center;
    background-color: #1d1d1d;
    max-width: rem(162);
    width: 100%;
    margin-inline: auto;
    padding-left: rem(36);
    margin-bottom: rem(60);
}

.p-header__drawer-nav-item {
    color: #fff;
    font-size: rem(16);
    margin-bottom: rem(21);
    display: block;
    text-align: left;
    white-space: nowrap;
    z-index: 1;
}

.nav-item1,
.nav-item2,
.nav-item3,
.nav-item4,
.nav-item5,
.nav-item6 {
    position: relative;
}

.nav-item1::before {
    content: "01";
    position: absolute;
    top: 16%;
    left: -10%;
    transform: translate(-50%, -50%);
    font-size: rem(36);
    color: #333333;
    z-index: -1;
    font-family: $font-accent;
    font-weight: bold;
}

