@use 'global' as *;


.p-recruit{
  

}
.p-recruit__inner{
  background-color: #333333;
  border-radius: rem(10);
  margin:rem(50) rem(10) rem(45) rem(10);
  padding:  rem(60) rem(15) rem(22) rem(27);
}
.p-recruit__heading{
  text-align: center;
  color: $white;
}
.p-recruit__en{
  font-size: rem(20);
  font-family: $font-sub;
  margin-bottom: rem(10);
}
.p-recruit__ja{
  font-size: rem(12);
  font-family: $font-yu;
  margin-bottom: rem(40);
}
.p-recruit__box{
  position: relative;
}
.p-recruit__no{
  font-size: rem(20);
  font-family: $font-sub;
  color: $orange;
  position: absolute;
  top: rem(13);
  left: rem(-10);
}
.p-recruit__ja2{
  color: $orange;
  font-family: $font-sub;
  background-color: #dddddd;
  border-radius: rem(3);
  padding-left: rem(30);
  padding-top: rem(17);
  padding-bottom: rem(17);

}

.p-recruit__item{
  display: flex;
  flex-direction: row-reverse;
  padding: rem(15) rem(10) rem(24.5) rem(30);



}
.p-recruit__detail{
  color: $white;
  font-size: rem(14);
  line-height: 1.85;
  font-family: $font-yu;
  letter-spacing: normal;
  margin-left: rem(20);

}
.p-recruit__left1 {
  margin-left: rem(-30);
  padding-top: rem(4);
}
.p-recruit__left1 img{
  min-width: rem(8);


}
.p-recruit__left2 {
  margin-left: rem(-30);
  margin-top: rem(4);


}
.p-recruit__left2 img{
  min-width: rem(8);

}
.p-recruit__left3 {
  margin-left: rem(-30);
  margin-top: rem(4);
}
.p-recruit__left3 img{
  min-width: rem(8);
}
.p-recruit__detail1{
  color: $white;
  font-size: rem(14);
  line-height: 1.85;
  font-family: $font-yu;
  letter-spacing: normal;
}
