@use 'global' as *;

.p-can {
  position: relative;
}
.p-can__inner {
  text-align: center;
}
.p-can__head img {
  position: absolute;
  top: rem(13);
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: rem(360);
  width: 100%;
  height: rem(87);
  margin-inline: auto;
}
.p-can__text {
  margin-top: rem(67);
  font-size: rem(15);
}

.p-can__head {
  padding: rem(2) rem(12);
}
