@use 'global' as *;
.p-curriculum {
    margin-top: rem(30);
    background: linear-gradient( 90deg, #333333 0%, #333333 94.8%, transparent 94.8%, transparent 100%);
    padding-top: rem(29);
    padding-bottom: rem(38);
}

.p-curriculum__inner {
    color: $white;
}

.p-curriculum__title {
    margin-bottom: rem(28);
}

.p-curriculum__box {
    padding-right: rem(15);
    padding-left: rem(10);
}

.p-curriculum__label {
    margin-left: calc(50% - 50vw);
    margin-bottom: rem(11);
    font-style: italic;
    @include mq(md) {
        margin-left: rem(-35);
    }
}

.p-curriculum__head {
    margin-bottom: rem(11);
    font-size: rem(20);
}

.p-curriculum__text {
    font-size: rem(12);
    line-height: calc(24 / 12);
    padding-bottom: rem(30);
    border-bottom: 1px solid $white;
    margin-bottom: rem(27);
    font-weight: normal;
    letter-spacing: 0em;
}

.l-inner>.p-curriculum__box:nth-of-type(5)>.p-curriculum__text {
    border-bottom: none;
    padding-bottom: 0;
    margin-bottom: 0;
}