@use 'global' as *;
.p-modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.5);
    padding-top: rem(90);
    padding-bottom: rem(90);
    overflow-y: scroll;
}

.p-modal.is-show {
    display: block;
    transition: 0.3s ease-in-out;
}

.p-modal__inner {
    margin-inline: auto;
}

.p-modal__wrapper {
    position: relative;
    padding: rem(40) rem(25) rem(45);
    background-color: $gray;
    border-radius: rem(3);
    overflow: hidden;
}

.p-modal__wrapper img {
    padding-left: rem(10);
    margin-bottom: rem(30);
    object-fit: cover;
    aspect-ratio: 310/ 195;
}

.p-modal__number {
    width: rem(48);
    height: rem(48);
    background-color: $blue;
    border-radius: 50%;
    color: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: rem(12);
    position: absolute;
    top: rem(25);
    left: rem(25);
}

.p-modal__position,
.p-modal__name {
    background-color: $gray2;
    font-size: rem(12);
    padding: rem(5) rem(4);
    display: inline-block;
    white-space: nowrap;
    margin-bottom: rem(3);
    color: $white;
    letter-spacing: 0.09em;
}

.p-modal__box {
    margin-top: rem(-84);
    margin-bottom: rem(24);
}

.p-modal__position {}

.p-modal__name {}

.p-modal__text {
    margin-bottom: rem(30);
    font-size: rem(12);
    line-height: calc(22 / 12);
    font-family: $font-yu;
}

.p-modal__head {
    font-size: rem(12);
    line-height: 1;
    font-family: $font-yu;
    padding: rem(5) rem(15);
    background-color: $black;
    display: inline;
    border-radius: rem(3);
    color: $white;
}

.p-modal__block {
    display: flex;
    font-size: rem(12);
    position: relative;
    margin-top: rem(14);
}

.p-modal__block::before {
    content: "";
    width: rem(7);
    height: rem(7);
    background-color: $black;
    border-radius: 50%;
    margin-right: rem(12);
    margin-top: rem(4);
}

.p-modal__years {
    white-space: nowrap;
    flex-basis: rem(40);
    margin-right: rem(12);
}

.p-modal__month {
    display: inline-block;
    text-align: right;
    width: rem(30);
    margin-right: rem(12);
    white-space: nowrap;
}

.p-modal__career {
    font-family: $font-kaku;
    font-weight: 300;
}

.p-modal__line1 {
    position: relative;
}

.p-modal__line1::after {
    content: "";
    position: absolute;
    top: rem(6);
    left: 1%;
    transform: translateX(-50%);
    width: rem(1);
    height: rem(150);
    background-color: $black;
}

.p-modal__line2 {
    position: relative;
}

.p-modal__line2::after {
    content: "";
    position: absolute;
    top: rem(6);
    left: 1%;
    transform: translateX(-50%);
    width: rem(1);
    height: rem(162);
    background-color: $black;
}

.p-modal__line3 {
    position: relative;
}

.p-modal__line3::after {
    content: "";
    position: absolute;
    top: rem(6);
    left: 1%;
    transform: translateX(-50%);
    width: rem(1);
    height: rem(100);
    background-color: $black;
}

.p-modal__close {
    position: absolute;
    top: rem(20);
    right: rem(0);
    width: rem(20);
    height: rem(20);
    margin-right: auto;
}

.p-modal__close span:nth-child(1) {
    display: block;
    width: rem(1);
    height: rem(12);
    background-color: $black;
    transform: rotate(-135deg);
}

.p-modal__close span:nth-child(2) {
    display: block;
    width: rem(1);
    height: rem(12);
    background-color: $black;
    transform: rotate(135deg);
    margin-top: rem(-12);
}