@use 'global'as *;

.p-contact {
  background-color: #dddddd;
  padding-top: rem(113);
}

.p-contact__inner {
}

.p-contact__title.c-section-title {
  text-align: center;
  position: relative;
  z-index: 2;
}

.p-contact__form {
  background-color: $white;
  margin: rem(0) rem(10);
  border-radius: rem(10);
  padding: rem(0) rem(15) rem(40) rem(15);
  width: calc(100% - 20px);
  margin-top: rem(-9);
  position: relative;
  z-index: 5;
}

.p-contact__sub {
  text-align: center;
  font-family: $font-yu;
  padding: rem(40) percentage(0) rem(12) rem(0);
  font-weight: 400;
}

.p-contact__text {
  font-size: rem(12);
  font-family: $font-kaku;
  font-weight: 300;
  text-align: center;
  margin-bottom: rem(30);
  line-height: 2;
}

.p-contact_item {
  font-size: rem(14);
  font-family: $font-kaku;
  font-weight: 300;
  margin-bottom: rem(15);
}

.p-contact_items {
  display: block;
  font-size: rem(14);
  font-family: $font-yu;
}

.p-contact__list {
  font-size: rem(14);
  font-family: $font-yu;
  margin-bottom: rem(5);
}

.p-contact__radioarea {
  margin-bottom: rem(29);
}

.p-contact__radio {
  margin-left: rem(0.8);
}
.p-contact__input {
  margin-bottom: rem(12);
  width: 100%;
  height: rem(38);
  padding: rem(0) rem(8);
}

.p-contact__textarea {
  resize: none;
  height: rem(128);
  width: 100%;
  padding: rem(8);
  margin-bottom: rem(27.2);
}
.p-contact__button {
  font-size: rem(14);
  font-family: $font-yu;
  display: block;
  text-align: center;
  border: rem(1) solid $black;
  border-radius: rem(28);
  padding: rem(15.5) rem(0);
  margin: rem(0) rem(25);
}

.p-contact__snsarea {
  padding: rem(40) rem(25) rem(50) rem(25);
  display: flex;
  gap: 20px;
}
.p-contact__snsarea img {
}

.p-contact__line {
}

.p-contact__insta {
}
