@use 'global' as *;

.p-job {
}
.p-job__bg {
  background-color: $sub-black;
  margin-right: rem(7);
  margin-left: rem(11);
  border-radius: rem(10);
  padding-top: rem(9);
  padding-bottom: rem(36);
  margin-bottom: rem(36);
}
.p-job__inner {
  padding-right: rem(15);
  padding-left: rem(15);
}
.p-job__titleblock {
}
.p-job__title {
  margin-left: rem(-24);
}
.p-job__title2 {
  margin-bottom: rem(14);
}
.p-job__text {
  margin-bottom: rem(36);
  font-size: rem(12);
  line-height: calc(24 / 12);
  color: $white;
  padding-right: rem(9);
  padding-left: rem(9);
}
.p-job__img1 {
}
.p-job__wrap {
  display: flex;
  margin-bottom: rem(28);
}
.p-job__img2 + .p-job__img2 {
  margin-left: rem(5);
}
.p-job__img2:nth-child(3) {
  margin-left: rem(10);
}
.p-job__img1 {
}
.p-job__img1 {
}
.p-job__panel-area {
}
.p-job__panel {
  background-color: #dddddd;
  padding: rem(50) rem(26) rem(30);
  border-radius: rem(3);
}

.p-job__img2 {
  margin-bottom: rem(16);
  position: relative;
  cursor: pointer;
}
.p-job__number {
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: rem(35);
  height: rem(35);
  text-transform: uppercase;
  font-family: $font-sub;
  background-color: $white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: rem(30);
  color: $orange;
}
.p-job__number::before {
  content: "";
  position: absolute;
  top: -44%;
  left: 33%;
  transform: translate(-50%, -50%);
  height: rem(12);
  width: rem(12);
  border-top: rem(1) solid $black;
  border-left: rem(1) solid $black;
  transform: rotate(45deg);
}
.p-job__head {
  text-align: center;
  font-size: rem(20);
  margin-bottom: rem(16);
}
.p-job__food {
  font-size: rem(12);
  line-height: 2;
}
.p-job__panel {
  display: none;
}
.p-job__panel.active {
  display: block;
}
