@use 'sass:math';

// インナー幅
$innerWidth: 345px;

// 余白
$padding-pc: 26px;
$padding-sp: 20px;

// フォント
@font-face {
  font-family: "galvji";
  src: url("./font/galvji.ttf") format("ttf");
  // src: url("./font/galvji.woff") format("woff");
  // src: url("./font/galvji.woff2") format("woff2");
}
$font-kaku: "Noto Sans", sans-serif;
$font-yu: "Noto Sans JP", YuGothic, "游ゴシック Medium", "Yu Gothic Medium",
  "游ゴシック", "Yu Gothic", sans-serif;
$font-sub: "Yantramanav", sans-serif;
$font-accent: "galvji", sans-serif;

// 色指定
$black: #000000;
$sub-black: #1d1d1d;
$white: #fff;
$gray: #c6c6c6;
$gray2: #535353;
$gray3: #b2b5ba;
$orange: #ff6200;
$blue: #6c798e;
// ブレイクポイント
$breakpoints: (
  sm: 600,
  md: 768,
  lg: 1024,
  xl: 1440,
);

// メディアクエリ
$mediaquerys: (
  sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
);

// メディアクエリを "@include mq(){}" で書くことができる
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}
