@use 'global' as *;
.p-hope {
    position: relative;
    z-index: 10;
    scroll-margin-top: rem(200);
    scroll-behavior: smooth;
    padding-top: 100px;
    margin-top: -100px;
}

.p-hope__title {
    text-align: center;
    max-width: rem(347);
    width: 100%;
    margin-inline: auto;
}

.p-hope__bg {
    background-color: #333333;
    margin-left: rem(20);
    padding: rem(42) rem(35) rem(21) rem(15);
    margin-top: rem(-10);
}

.p-hope__inner {
    color: $white;
    text-align: center;
    padding: 0;
}

.p-hope__top {
    font-size: rem(15);
    margin-bottom: rem(11);
    position: relative;
    white-space: nowrap;
}

.p-hope__top:first-child::before {
    content: "";
    display: inline-block;
    width: rem(18);
    height: rem(1);
    background-color: $white;
    transform: rotate(45deg);
    vertical-align: middle;
    margin-right: rem(8);
    margin-top: rem(-2);
}

.p-hope__top:first-child::after {
    content: "";
    display: inline-block;
    width: rem(18);
    height: rem(1);
    background-color: $white;
    transform: rotate(135deg);
    vertical-align: middle;
    margin-left: rem(8);
    margin-top: rem(-2);
}

.l-inner>.p-hope__top:first-of-type {
    display: inline-block;
}

.l-inner>.p-hope__top:first-of-type::before {
    content: "";
    position: relative;
    display: inline-block;
    width: rem(18);
    height: rem(1);
    background-color: $white;
    z-index: 10;
    transform: rotate(45deg);
    vertical-align: middle;
    margin-right: rem(6);
    margin-top: rem(-2);
}

.p-hope__inner>.p-hope__top:first-of-type::after {
    content: "";
    position: relative;
    display: inline-block;
    width: rem(18);
    height: rem(1);
    background-color: $white;
    z-index: 10;
    transform: rotate(135deg);
    vertical-align: middle;
    margin-left: rem(6);
    margin-top: rem(-2);
}

.p-hope__head {
    display: inline-block;
    padding: rem(2) rem(34);
    background-color: #dddddd;
    color: $orange;
    text-align: center;
    font-size: rem(20);
    margin-bottom: rem(11);
    white-space: nowrap;
    line-height: 1;
}

.p-hope__img {
    margin-top: rem(36);
    margin-bottom: rem(36);
    margin-left: calc(50% - 50vw);
    width: 100%;
    min-width: rem(355);
    @include mq(md) {
        padding-left: calc(50vw - 61%);
        max-width: none;
        width: auto;
    }
}

.p-hope__inner>.p-hope__img:last-of-type {
    margin-bottom: rem(31);
    margin-top: rem(29);
}

.p-hope__foot {
    font-size: rem(15);
    line-height: 1.66;
    margin-bottom: rem(18);
}

.p-hope__text {
    font-family: $font-kaku;
    font-size: rem(12);
    text-align: left;
    line-height: calc(28 / 12);
    letter-spacing: 0.04em;
    font-weight: 300;
}

.p-hope__label {
    font-style: italic;
    display: table;
    margin-bottom: rem(11);
    margin-left: calc(50% - 50vw);
    @include mq(md) {
        margin-left: rem(-35);
    }
}

.p-hope__point {
    white-space: nowrap;
    font-size: rem(20);
    margin-bottom: rem(21);
    text-align-last: left;
}