@use 'global' as *;

.p-message__inner.l-inner {
  padding-left: rem(35);
  padding-right: rem(35);
  position: relative;
  font-family: $font-kaku;
}
.p-message__img {
  margin-right: calc(50% - 50vw);
  object-fit: cover;
  margin-bottom: rem(40);
  @include mq(md) {
    margin-right: rem(-35);
  }
}
.p-message__titleblock {
}
.p-message__title {
  position: absolute;
  top: rem(-36);
  left: 0;
}
.p-message__copy {
  margin-bottom: rem(40);
}
.p-message__text {
  font-size: rem(17);
  font-weight: 500;
  margin-bottom: rem(14);
  background-color: #dddddd;
  color: #000000;
  display: table;
  line-height: 1;
  padding: rem(7) rem(7) rem(7) rem(7);
  letter-spacing: 0.04em;
  white-space: nowrap;
}
.p-message__text2{
  font-size: rem(14);
  font-family: $font-kaku;
  line-height: calc(26 / 14);

}
.p-message__text4 {
  font-size: rem(14);
  line-height: 1.85;
  letter-spacing: normal;
}
.p-message__img2 {
  margin-top: rem(75);
  margin-left: calc(50% - 50vw);
  object-fit: cover;
  @include mq(md) {
    margin-left: rem(-35);
  }
}
